\:root
    font-size: 14px
    font-family: Mulish

*, *::before, *::after
    margin: 0
    padding: 0
    box-sizing: border-box

.primary-color
    color: #EB5757

.btn
    background-color: #EB5757

.container
    padding: 15px
    max-width: 1300px
    margin: auto

.navbar
    display: flex
    justify-content: space-between
    align-items: center
    flex-direction: column
    &__open
        box-shadow: 0 0 0 200vh rgba(0,0,0,0.5)
        align-items: flex-start
        max-height: 632px
        position: fixed
        background: white
        top: 0
        width: 100%
        left: 0
        padding: 10px
        & .close-navbar-icon-desktop
            display: none

.navbar-top
    display: flex
    align-items: center
    justify-content: space-between
    width: 100%
    margin-bottom: 15px

.close-navbar-icon, .close-navbar-icon-desktop
    display: grid
    place-items: center
    padding: 2px
    background: none
    border: none
    cursor: pointer

.location
    color: #4F4F4F
    display: flex
    align-items: center
    gap: 10px
    width: 100%
    &__left-content
        display: flex
        gap: 15px
        align-items: center
        margin-right: auto

.location-list
    margin: 15px 0
    display: flex
    flex-direction: column
    list-style: none
    &__item
        cursor: pointer
        width: 100%
        display: flex
        justify-content: space-between
        padding: 5px 0

.navbar-title
    font-weight: bold
    font-size: 0.9rem

.filters-container
    border-radius: 16px
    box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1)
    width: 100%

.filter-container
    padding: 11px 25px
    align-items: center

.filter-title
    cursor: pointer

.search-button
    background-color: #EB5757
    border-radius: 16px
    border: none
    display: flex
    padding: 12px 21px
    color: white
    display: flex
    align-items: center
    margin: 25px auto
    cursor: pointer
    span
        color: white
        margin-right: 15px

.between
    display: flex
    justify-content: space-between

.align-center
    align-items: center

.filter-label
    font-weight: bold
    font-size: 0.7rem
    text-transform: uppercase
    margin-bottom: 3px

.filter-container-content
    margin-top: 15px

.divider-horizontal
    border-top: thin solid #F2F2F2
    width: 100%

.logo
    width: 100%
    height: 40px
    margin-top: 10px
    margin-bottom: 20px

.guests-sub-title
    font-weight: 700

.guests-action-buttons
    display: flex
    align-items: center
    margin-top: 15px
    gap: 15px
    & button
        border: none
        background: none
        border-radius: 4px
        border: thin solid black
        display: grid
        place-items: center
        &:disabled
            border: thin solid #F2F2F2

.smallNavActions
    cursor: pointer
    display: flex
    height: 55px
    width: 297px
    box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.1)
    border-radius: 16px
    align-items: center
    span
        margin: auto
    *
        padding: 15px

.faint-text
    color: #BDBDBD

.boxed
    border-left: thin solid #F2F2F2
    border-right: thin solid #F2F2F2

.row
    display: flex

.stays-title
    align-items: center
    display: flex
    margin-top: 30px
    margin-bottom: 25px

.room-cards-grid-list
    list-style: none
    display: grid
    gap: 30px
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr))

.room-card
    width: 100%
    &__image
        width: 100%
        border-radius: 24px
        max-height: 238px
        object-fit: cover
        margin-bottom: 8px
    &__title
        font-weight: 800

.room-card-info
    display: flex
    align-items: center
    margin-bottom: 10px
    &__super-host
        font-weight: 800
        border: thin solid black
        display: inline-block
        padding: 4px 10px
        font-size: 0.8rem
        border-radius: 15px
        margin-right: 10px
    &__details
        display: flex
        align-items: center
        justify-content: space-between
        flex-grow: 1
    &__rating
        display: flex
        align-items: center
        span
            color: #EB5757
            font-size: 1.5rem

@media only screen and ( min-width: 800px )
    .navbar-top
        display: none
    .filters-container
        display: flex
        align-items: flex-start
        justify-content: space-evenly
        height: 300px
    .filter-container
        width: 300px
    .search-button
        margin: 0
    .divider-horizontal
        display: none
    .navbar__open
        & .close-navbar-icon-desktop
            display: block
            margin-top: 10px

    .filters-container
        box-shadow: none

    .navbar__closed
        flex-direction: row
    .logo
        width: initial
        height: 30px
